<template>
  <div class="main">
    <div class="title">
      <div class="row">
        <h3>
          [mygo]
          mygo()(13/13)
        </h3>
        <el-link
          href="https://bbs.nga.cn/read.php?tid=38447162"
          target="_blank"
          :underline="false"
          >
        </el-link>
      </div>
      <div class="row">
        <el-input
          placeholder=""
          v-model="floor"
          @change="floorChange()"
          clearable
        />
        <el-input
          placeholder=""
          prefix-icon="el-icon-search"
          v-model="search"
          clearable
        />
        <el-date-picker
          v-model="timerange"
          type="datetimerange"
          start-placeholder=""
          end-placeholder=""
          format="yyyy-MM-dd HH:mm"
          value-format="yyyy-MM-dd HH:mm"
          :clearable="false"
        />
        <el-switch
          v-model="mode"
          inactive-text=""
          active-text=""
        />
      </div>
      <div class="row">
        <el-pagination
          @current-change="handleCurrentChange"
          @size-change="handleSizeChange"
          :current-page="currentPage"
          :page-sizes="[20, 50, 100, 200, 500, 2000]"
          :page-size="pageSize"
          layout="total,sizes, prev, pager, next, jumper"
          :total="filteredFloor.length"
        >
        </el-pagination>
      </div>
    </div>
    <div class="content">
      <div v-for="item in currentFloorDetail" :key="item.floor">
        <div v-if="item.reply == ''">
          <Floor :floorDetail="item"></Floor>
        </div>
        <div v-else>
          <el-popover placement="top" trigger="hover">
            <p>{{ getPopoverContent(item.reply).username }}</p>
            <div
              v-html="getPopoverContent(item.reply).comment"
              class="reply"
            ></div>
            <div slot="reference"><Floor :floorDetail="item"></Floor></div>
          </el-popover>
        </div>
      </div>
      <el-empty description="" v-if="filteredFloor == ''">
        <el-button type="primary" @click="refreshPage"> </el-button>
      </el-empty>
      <div class="under">
        <el-pagination
          @current-change="handleCurrentChange"
          @size-change="handleSizeChange"
          :current-page="currentPage"
          :page-sizes="[20, 50, 100, 200, 500, 2000]"
          :page-size="pageSize"
          layout="total,sizes, prev, pager, next, jumper"
          :total="filteredFloor.length"
        >
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import Floor from "@/components/posts/Floor.vue";
// 
import floorDetail from "@/assets/38447162";
export default {
  components: {
    Floor,
  },
  data() {
    return {
      floorDetail,
      // 
      floor: "",
      search: "",
      timerange: ["2023-11-19 00:00", "2099-11-19 00:00"],
      // false,true
      mode: false,
      // 
      currentPage: 1,
      // 
      pageSize: 50,
    };
  },
  computed: {
    // 
    filteredFloor() {
      let filteredFloor = this.floorDetail;
      // 
      if (
        this.search == "" &&
        this.timerange[0] == "2023-11-19 00:00" &&
        this.timerange[1] == "2099-11-19 00:00" &&
        !this.mode
      ) {
        return filteredFloor;
      }
      // 
      if (this.search != "") {
        filteredFloor = filteredFloor.filter(
          (item) =>
            item.username.includes(this.search) ||
            item.comment.includes(this.search)
        );
      }
      // 
      if (
        this.timerange[0] != "2023-11-19 00:00" ||
        this.timerange[1] != "2099-11-19 00:00"
      ) {
        filteredFloor = filteredFloor.filter((item) => {
          const timeInRange =
            new Date(item.time) >= new Date(this.timerange[0]) &&
            new Date(item.time) <= new Date(this.timerange[1]);
          return timeInRange;
        });
      }
      // 
      if (this.mode) {
        filteredFloor = filteredFloor.filter(
          (item) => item.username == "kngchau"
        );
      }
      return filteredFloor;
    },
    // 
    currentFloorDetail() {
      const startIndex = (this.currentPage - 1) * this.pageSize;
      const endIndex = startIndex + this.pageSize;
      return this.filteredFloor.slice(startIndex, endIndex);
    },
  },
  methods: {
    // 
    floorChange() {
      const element = document.getElementById(this.floor);
      if (element) {
        element.scrollIntoView();
      } else if (this.floor != "") {
        this.$message.error("");
      }
    },
    // 
    refreshPage() {
      window.location.reload();
    },
    // 
    handleCurrentChange(page) {
      this.currentPage = page;
      window.scrollTo(0, 0);
    },
    // 
    handleSizeChange(size) {
      this.pageSize = size;
    },
    // 
    getPopoverContent(reply) {
      return this.floorDetail.find((item) => item.floor === reply);
    },
  },
};
</script>

<style lang="less" scoped>
.title {
  display: flex;
  flex-wrap: wrap;
  gap: 2vw;
  .row {
    width: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 2vw;
    h3,
    .el-input,
    .el-switch,
    .el-date-picker {
      flex: 1;
    }
    .el-switch {
      height: 100%;
    }
  }
}
.under {
  margin: 5vh auto;
  display: flex;
  justify-content: center;
}
.reply {
  max-width: 60vw;
}
</style>